.service-container {
    list-style-position: inside;
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.service-container li {
    padding: 30px;
    margin: 15px;
    text-align: left;
    cursor: pointer;
    background: rgba(0,0,0,0.5);
    /*width: 50%;*/
}

@media only screen and (max-width: 900px) {
    .service-container {
        list-style-type: none;
        grid-template-columns: 1fr;
    }
    .service-container li {
        text-align: center;
    }
}

