body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(to bottom, #771318, black);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

hr {
  color: white;
}

.container {
  padding: 0 5%;
}

@media only screen and (max-width: 600px) {
  .container {
      padding: 0 10%;
  }
}

.section-top {
  padding-top: 165px;
}

.section-middle {
  padding-top: 120px;
}

.section-bottom {
  padding-top: 225px;
}

.noselect {
  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */ /* Konqueror HTML */
       -moz-user-select: none; /* Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome and Opera */
}




.nav {
    background: rgba(0,0,0,0.88);
    position: fixed;
    top: 0px;
    width: 100%;
    height: 150px;
    z-index: 1000;
}
  
.nav-logo {
    height: 90px;
}
  
.nav-logo:hover {
    cursor: pointer;
}
  
.nav .nav-content {
    padding: 0rem 3rem;
    margin: 0 auto;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    height: 100%;
}


 
.nav-item {
    display: inline;
    margin-left: 3rem;
    color:white;
    font-size: 1.75rem;
}

.nav-item:active {
    color: red;
}
  
.nav-item:hover {
    opacity: 0.7;
    cursor: pointer;
}

@media only screen and (max-width: 1380px) {
    .nav .nav-content {
        padding: 0;
        margin: 0;
        
        display: block;
        
    }
    .nav-items {
        padding: 0;
        margin: 0;
        
        
    }
    .nav-logo {
        padding-top: 5px;
    }
    .nav-item {
        display: inline; 
        float: none;
        margin: 0;
        padding: 30px; 
             
     }
}

@media only screen and (max-width: 900px) {
    .nav .nav-content {
        display: -webkit-flex;
        display: flex;
        -webkit-justify-content: center;
                justify-content: center;
        -webkit-align-items: center;
                align-items: center;
        margin: 0 15px;
    }
            
    
    .nav-item {
        display: none;
    }
}

