.contact-form{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap:30px;
    padding: 0 5%;   
 }

.contact-form label{
    display:block;
    text-align: left;
    padding: 15px 0;
    font-size: 1.5rem;
  }

.contact-form p{
    margin:0;
}

.contact-form .full{
    grid-column: 1 / 3;
}

.contact-form input, 
.contact-form select,
.contact-form textarea{
    width:100%;
    padding:0.75em;
}

.contact-form input[type='submit'] { 
    width: 25%;
    color: #fff;
    background: #771318; 
    border: 0;
    margin: 15px;
}

.contact-form input[type='submit']:hover { 
    background: #990000; 
    cursor: pointer; 
}

.contact-form textarea {
    resize: vertical;
}

@media only screen and (max-width: 900px) {
    .contact-form p {
        grid-column: 1 / 3;
    }
    .contact-form input[type='submit'] { 
        width: 50%;
    }
  }