.nav {
    background: rgba(0,0,0,0.88);
    position: fixed;
    top: 0px;
    width: 100%;
    height: 150px;
    z-index: 1000;
}
  
.nav-logo {
    height: 90px;
}
  
.nav-logo:hover {
    cursor: pointer;
}
  
.nav .nav-content {
    padding: 0rem 3rem;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
}


 
.nav-item {
    display: inline;
    margin-left: 3rem;
    color:white;
    font-size: 1.75rem;
}

.nav-item:active {
    color: red;
}
  
.nav-item:hover {
    opacity: 0.7;
    cursor: pointer;
}

@media only screen and (max-width: 1380px) {
    .nav .nav-content {
        padding: 0;
        margin: 0;
        
        display: block;
        
    }
    .nav-items {
        padding: 0;
        margin: 0;
        
        
    }
    .nav-logo {
        padding-top: 5px;
    }
    .nav-item {
        display: inline; 
        float: none;
        margin: 0;
        padding: 30px; 
             
     }
}

@media only screen and (max-width: 900px) {
    .nav .nav-content {
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 0 15px;
    }
            
    
    .nav-item {
        display: none;
    }
}

