#container-block {
    width: 100%;
    display: block;
    margin: 0 auto;
    padding: 0% 21%;
}

@media only screen and (max-width: 800px) {
    #container-block {
        padding: 0 15%;
    }
}

@media only screen and (max-width: 500px) {
    #container-block {
        padding: 0 5%;
    }
}

.vimeo-wrapper {
    position: relative;
    padding-bottom: 56.25%; 
    height: 0;
  }
  
.vimeo-wrapper iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

